import uniRate from '../../../components/uni-ui/uni-rate/uni-rate.vue'
export default {
	name: "",
	components: {
		uniRate
	},
	props: {},
	data() {
		return {
			trailerId: '', //搜索热门传过来的id
			hotdetails: {}, //获取到的电影详情
			participant: [], //演职人员
			stills:[],//剧照
		}
	},
	methods: {
		getDetails() { //获取热门详情
			uni.request({
				url: `${this.$api}/search/trailer/${this.trailerId}?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.hotdetails = res.data.data;
					// console.log(this.hotdetails)
					this.getdirector()
					this.stills = JSON.parse(res.data.data.plotPics)
					// console.log(this.stills)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取导演
		getdirector() {
			uni.request({ //获取导演
				url: `${this.$api}/search/staff/${this.trailerId}/1?&qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					res.data.data.map(item => {
						this.participant.push(item)
					})
					this.getactor()
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取演员列表
		getactor() {
			// let actor = []				
			//获取演员
			uni.request({ //获取导演
				url: `${this.$api}/search/staff/${this.trailerId}/2?&qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					res.data.data.map(item => {
						this.participant.push(item)
					})
				},
				fail: () => {},
				complete: () => {}
			});

			console.log(this.participant)
		},


	},

	mounted() {

	},
	onLoad(e) {
		this.trailerId = e.id
		// console.log(this.trailerId)
		this.getDetails()
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
